/* board_members_page_header */

#admin_board_members_page_header{
	height: 13vh;
	width: 100%;
	position: sticky;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0,0.95);
	color: var(--fourth-color);
	z-index: 3;
}

#admin_board_members_page_header_nav{
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

#admin_board_members_page_header_nav_img{
	width: 20%;
	height: 95%;
	border: solid white 1.5px;
	margin-left: 2%;
}

#admin_board_members_page_header_nav_h1{
	margin-right: 10%;
}

#admin_board_members_page_header_nav_a2{
	text-decoration: none;
	color: var(--fourth-color);
	padding: 1%;
	padding-left: 2%;
	padding-right: 2%;
	margin-right: 2%;
	border: solid var(--fourth-color) 2px;
}
#admin_board_members_page_header_nav_a2:hover{
	background-color: var(--fourth-color);
	color: black;
}

/* admin_board_members_page_main */

#admin_board_members_page_main{
	width: 100%;
	height: 110vh;
	padding-top: 3%;
	background-color: var(--third-color);
	color: var(--fourth-color);
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-y: scroll;
	padding-bottom: 5%;
}

#admin_board_members_page_main_div{

}

#admin_board_members_page_main_h2{
	background-color: var(--fourth-color);
	color: var(--third-color);
	padding: 1%;

}

#admin_board_members_page_main_h21{
	background-color: var(--fourth-color);
	color: var(--third-color);
	padding: 1%;

}

#admin_board_members_page_main_section{
	margin-top: 2%;
	width: 98%;
	display: flex;
	align-items: flex-start;
	flex-direction: column;
	justify-content: flex-start;
	box-sizing: border-box;

}

#admin_board_members_page_main_section_nav{

	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	flex-direction: row;
	height: 7vh;


}

#admin_board_members_page_main_section_nav_div{


display: flex;
flex-direction: row;
height: 100%;
min-width: 30%;
justify-content: center;
align-items: center;
	
}

.admin_board_members_page_main_section_nav_div_h5{

	display: flex;
	justify-content: center;
	align-items: center;
	padding: 1%;
	font-size: 105%;
	cursor: pointer;
	border-bottom: var(--fourth-color) solid 2px;
}
.admin_board_members_page_main_section_nav_div_h5:hover{
	background-color: white;
	color: var(--third-color);
}

#admin_board_members_page_main_section_nav_div_h51{
	cursor: default;
	margin-right: 2%;
	background-color: var(--main-color);
	color: var(--fourth-color);
	padding-left: 2%;
	padding-right: 2%;
	padding: 1%;
	border-bottom: none;
}
#admin_board_members_page_main_section_nav_div_h51:hover{
	background-color: var(--main-color);
	color: var(--fourth-color);
}

#admin_board_members_page_main_section_nav_div_h52{
	margin-right: 1.5%;

}

.admin_board_members_page_main_section_nav_div_h5_selected{
		background-color: white;
	color: var(--third-color);
}

#admin_board_members_page_main_section_ul1{

	width: 100%;
	box-sizing: border-box;
	padding: 3%;
	height: 75vh;
	overflow-y: scroll;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
}

.admin_board_members_page_main_section_ul1_li{

	width: 100%;
	margin-bottom: 1%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

.admin_board_members_page_main_section_ul1_li_a{
	background-color: var(--fourth-color);
	color: var(--third-color);
	padding: 1%;
	text-decoration: none;
	font-weight: 600;
}
.admin_board_members_page_main_section_ul1_li_a:hover{
	background-color: white;
	color: black;
}







